<script>
  import { debounce } from "lodash";
  import { mapState } from "vuex";
  export default {
    name: "Lib",
    props: {
      timeAnimate: {
        type: String,
        default: '.2',
        required: false
      }
    },
    data: () => ({
      test: false,
      isOpenFilterOption: false,
      onFilterOptionModel: 0,
      onFilterOptionModelChange: false,
      genreUserSelect: [],
      onFilterOptions: [
      {
        name: "",
      },
      {
        text: "Физическая копия",
        options: "physicalCopy",
        icon: "book-open-blank-variant"
      }, 
      {
        text: "Электронная копия",
        options: "onlineCopy",
        icon: "package-variant-closed"
      }, 
      {
        text: "От большего к меньшему",
        options: "higher",
        icon: "priority-high"
      }, 
      {
        text: "От меньшего к большему",
        options: "smaller",
        icon: "priority-low"
      },
    ],
    }),
    methods: {
      filterOptionsClickOutside() {
        return this.isOpenFilterOption = false;
      },
      onFilterOptionsSwitch() {
        return this.onFilterOptionModelChange = true;
      }
    },
    computed: {
      ...mapState({
        books: state => state.books.books.item,
        booksTip: state => state.books.tips,
        booksGenre: state => state.books.allGenres,
      }),
      animationFilterSwitch() {
        if(this.isOpenFilterOption) {
          return;
        }
        else {
          return this.onFilterOptionModelChange = !this.onFilterOptionModelChange; 
        }
      },
      resizerForImage() {
        switch (this.$vuetify.breakpoint.name) {
          case 'xs':
            return 95
          case 'sm':
            return 140
        }
        return 200;
      },
      filtersForBooks() {
        let badgeContentAfterFiltered = "";
        let booksNotFiltred = this.$store.state.books.books.item;
        let filteredBooks = [];
        // for ignore vue cli err overlay delete on prod
        switch(this.onFilterOptionModel) {
          case 0: 
            filteredBooks = booksNotFiltred;
            break;
          case 1: 
            filteredBooks = booksNotFiltred.filter((item) => item.copy.physical);
            break;
          case 2: 
            filteredBooks = booksNotFiltred.filter((item) => item.copy.online);
            break;
          case 3:
            filteredBooks = booksNotFiltred.sort((a, b) => {
              return a.price - b.price;
            });
            break;
          case 4: 
            filteredBooks = booksNotFiltred.sort((a, b) => {
              return b.price - a.price;
            });
            break;
        }

        switch(this.onFilterOptionModel) {
          case 1: 
            badgeContentAfterFiltered = "Только физ. копия"; 
            break;
          case 2: 
            badgeContentAfterFiltered = "Только онлаин копия"; 
            break;
          case 3: 
            badgeContentAfterFiltered = "Самая дешевая";
            break;
          case 4: 
            badgeContentAfterFiltered = "Самая дорогая";
            break;
        }

        filteredBooks.forEach(item => item.badgeContent = "");
        filteredBooks.forEach((item, i) => {
          if(i === 0 && this.onFilterOptionModel === 3 || this.onFilterOptionModel === 4) {
            filteredBooks[0].badgeContent = badgeContentAfterFiltered;
          }
          else if(i !== 0 && this.onFilterOptionModel === 3 || this.onFilterOptionModel === 4) {
            item.badgeContent = "";
          }
          else {
            item.badgeContent = badgeContentAfterFiltered;
          }
        });        
        return filteredBooks;
      },
    },
    created() {
      let funcForDebounce = [this.filtersForBooks, this.resizerForImage, this.books];
      funcForDebounce.forEach(item => {
        debounce(item, 7000);
      });
      return;
    }
  }
</script>
